/* Desktop */
@media (min-width: 1025px){
    .cardMiddle{
        width: 300px;
        height: 360px
    }
    .cardMiddle .newsImageDiv{
        height: 200px;
    }    
    .newsSection{
        display: grid;
        grid-template-columns: repeat(4,1fr);
    }
    .newsCard:nth-child(1){
        grid-column: 2;
        justify-self: right;
    }
    .newsCard:nth-child(2){
        grid-column: 3;
        justify-self: left;
    }
    .newsCard:nth-child(3){
        grid-column: 2;
        justify-self: right;
        margin-right: 13em;
    }
    .newsCard:nth-child(4){
        grid-column: 3;
        justify-self: left;
        grid-row: 2;
        margin-left: -10em;
    }
    .newsCard:nth-child(5){
        grid-column: 3;
        justify-self: left;
        grid-row: 2;
        margin-left: 12em;
    }
    .newsCard:nth-child(6){
        grid-column: 2;
        justify-self: right;
    }
    .newsCard:nth-child(7){
        grid-column: 3;
        justify-self: left;
    }

}
